<script>
    import VueCookies from 'vue-cookies'

    export default {
        name: "AppHeader", 
        data() {     
            return {
                statusApp: VueCookies.get('status')  
            } 
        },
        methods: {
            signOut() {
                VueCookies.set('login', '') 
                VueCookies.set('status', '')
                this.$router.push('/') 
            }
        }
    }; 
</script> 


