        .preview-section
            .preview-section__main 
                .preview-section__container.container
                    .preview-section__content
                        p.preview-section__entry  
                            | Скоринговая платформа от 
                            <span class="font--sb">Fscore</span> 
                            <span class="font--sb text--blue">Lab</span>

                        .preview-section__title Повысьте рентабельность кредитного бизнеса с помощью эффективных скоринговых технологий

                        .preview-section__button
                            span.btn.btn_land получить 100 бесплатных проверок

                    .preview-section__percent  
                        ICountUp( 
                            :startVal="ICountUp.startVal"
                            :endVal="ICountUp.endVal"
                            :decimals="ICountUp.decimals"
                            :duration="ICountUp.duration"
                            :options="ICountUp.options"
                            @ready="onReady"
                        )

            .preview-section__infographic
                .container
                    .preview-infographic-grid
                        .preview-infographic-grid__item(v-for="previewItem in previewSection.infoGrid")
                            .preview-infographic-item__circle
                                img(:src="previewItem.icon").why-infographic-item__icon
                            .preview-infographic-item__text {{previewItem.text}}

   

        .why-section
            .container
                .why-section__title
