        .page-footer
            .page-footer__container.container
                .page-footer__section
                    .page-footer__logo
                        .logo.logo_fz_22
                            | Cash 
                            span.text--blue Machine 
                    .page-footer__copy Программная модель и авторские права принадлежат FscoreLab ltd
                .page-footer__section
                    .page-footer__nav.page-nav.page-nav_footer
                        a(href='#').page-nav__item Все решения
                        a(href='#').page-nav__item О нас
                        a(href='#').page-nav__item Новости 

                    .page-footer__soc 
                        a(href='#').btn.btn_icon.btn_bor_white
                            svg.btn__icon(
                                xmlns="http://www.w3.org/2000/svg"
                                xmlns:xlink="http://www.w3.org/1999/xlink"
                                width="18px" height="18px"
                            )
