                            .btn__text Наша группа 
                .page-footer__section
                    .page-footer__address 
                        .address-box
                            svg.address-box__icon(
                                xmlns="http://www.w3.org/2000/svg"
                                xmlns:xlink="http://www.w3.org/1999/xlink"
                                width="22px" height="31px"
                            )
                                path(
                                    fill-rule="evenodd" 
                                    fill="rgb(255, 255, 255)"
                                    d="M22.000,2.059 L1.792,2.059 L1.792,-0.000 L-0.000,-0.000 L-0.000,31.000 L1.792,31.000 L1.792,15.222 L22.000,15.222 L15.870,8.640 L22.000,2.059 Z"
                                )
                            p Санкт-петербург, ул. 5-я советская, 16, литера А, пом. 7н, оф. 3-цб
  
    
        .modal(v-if="popupOpened != '' ") 
            .modal__overlay
            .popup(v-if="popupOpened === 'reg'")
                form(@submit.prevent='handleSubmit').popup__form.popup-form
                    header.popup-form__header.popup-form__header_pb10
                        .popup-form__title Регистрация
                        .popup-form__steps
                            .sm-info-box Шаг {{regStep}}/2 
                        .popup-form__close 
                            .close-btn(@click="closePopups")

                    .popup-form__main 
                        .form-grid(v-if="regStep === 1") 
                            .form-grid__item 
                                input(type="text" placeholder="ФИО администратора" required).st-input
                            .form-grid__item 
                                input(type="text" placeholder="Телефон").st-input
                            .form-grid__item 
                                input(type="text" placeholder="Название компании").st-input
                            .form-grid__item 
                                input(type="text" placeholder="E-mail").st-input
                            .form-grid__item 
                                .form-group(:class="{ 'form-group--error': $v.password.$error }")
                                    input(v-model.trim="$v.password.$model" type="password" placeholder="Пароль").st-input
                                    .form-group__error(v-if="!$v.password.minLength")  
                                        | Пароль должен содержать не менее {{ $v.password.$params.minLength.min }} символов. 
                            .form-grid__item 
                                .form-group(:class="{ 'form-group--error': $v.repeatPassword.$error }")
                                    input(v-model.trim="$v.repeatPassword.$model" type="password" placeholder="Пароль еще раз").st-input
                                .form-group__error(v-if="!$v.repeatPassword.sameAsPassword") Пароли должны быть идентичны. 

                        .popup-form__descr(v-if="regStep === 2") Необходимо подтвердить адрес электронной почты. Для этого перейдите по ссылке из письма об активации аккаунта.

                    .popup-form__footer.tac(v-if="regStep === 1")
                        button.btn Зарегистрироваться
                    .popup-form__footer.tac(v-if="regStep === 2")
                        button.btn Отправить повторно

                .popup__footer.popup-footer
                    span.popup-footer__text Уже есть аккаунт?
                    button(@click="openPopup('log')").btn.btn_trsp-small Войти

            .popup(v-if="popupOpened === 'log'")
                form(@submit.prevent='handleSubmit').popup__form.popup-form
                    header.popup-form__header
                        .popup-form__title Вход в систему
                        .popup-form__close
                            .close-btn(@click="closePopups")

                    .popup-form__main 
                        .form-grid 
                            .form-grid__item 
                                input(type="text" placeholder="E-mail").st-input
                            .form-grid__item 
                                input(type="text" placeholder="Пароль").st-input

                    .popup-form__footer
                        button.btn Войти  
                        span(@click="openPopup('password')").st-link Забыли пароль?
                    .popup-form__footer
                        button.btn(@click="authManager()") Войти как Менеджер  
                    .popup-form__footer
                        button.btn(@click="authUser()") Войти как Пользователь
               

                .popup__footer.popup-footer
                    span.popup-footer__text Нет аккаунта?
                    button(@click="openPopup('reg')").btn.btn_trsp-small Регистрация


            .popup(v-if="popupOpened === 'password'") 
                form(@submit.prevent='handleSubmit').popup__form.popup-form
                    header.popup-form__header
                        .popup-form__title Восстановление пароля
                        .popup-form__close
                            .close-btn(@click="closePopups")

                    .popup-form__main 
                        .form-grid 
                            .form-grid__item 
                                input(type="text" placeholder="E-mail").st-input 
                            .form-grid__item 
                                button.btn Восстановить

                        
    
    </div> 
  
 
</template> 
  

<script> 
    import Footer from '@/components/Footer.vue' 
    
    import data from '../localization/index.json'  
    import ICountUp from 'vue-countup-v2'
    import { validationMixin } from 'vuelidate' 
    import { required, sameAs, minLength } from 'vuelidate/lib/validators' 
    import VueCookies from 'vue-cookies'
    
    const lang = 'ru'; 

    export default {
        name: "Landing",  
        components: {
            ICountUp, 
            Footer 
        }, 
        mixins: [validationMixin], 
        data() {    
            return {
                ICountUp: {
                    startVal: 0,
                    endVal: 88,
                    decimals: 0,
                    duration: 7,
                    options: {
                        useEasing: true,
                        useGrouping: true,
                        separator: ',',
                        decimal: '.',
                        prefix: '',
                        suffix: '%' 
                    } 
                },
                previewSection: data[lang].previewSection,
                whySection: data[lang].whySection,
                advantagesSection: data[lang].advantagesSection,
                startSection: data[lang].startSection, 
                technologiesSection: data[lang].technologiesSection,
                teamSection: data[lang].teamSection, 
                tariffsSection: data[lang].tariffsSection,
                popupOpened: '',
                regStep: 1,
                regForm: {
                    name: '',
                    phone: '',
                    nameCompany: '',
                    email: '',
                    password: '',
                    repeatPassword: '' 
                },
                password: '',
                repeatPassword: ''
            }
        }, 
        methods: { 
            onReady: function(instance, CountUp) {
                const that = this;
                instance.update(that.endVal + 100);
            }, 
            handleSubmit() { 
                this.regStep = 2;  
            },
            openPopup(name) {  
                this.popupOpened = name 
                
            }, 
            closePopups() {
                this.popupOpened = '' 
            },
            authManager() {
                VueCookies.set('login', 'login') 
                VueCookies.set('status', 'manager')
                this.$router.push('/manager')
            },
            authUser() {
                VueCookies.set('login', 'login')
                VueCookies.set('status', 'user') 
                this.$router.push('/user')
            },
        },
        validations: { 
            password: {
                required,
                minLength: minLength(6)
            },
            repeatPassword: { 
                sameAsPassword: sameAs('password')
            }
        }
    };
</script>  