import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import VueCookies from 'vue-cookies'

import App from './views/App.vue' 

import Archive from './views/Archive.vue'
import Profile from './views/Profile.vue'
import Company from './views/Company.vue'
import CheckAdmin from './views/CheckAdmin.vue'
import Check from './views/Check.vue'
import ChangePassword from './views/ChangePassword.vue'

import PageNotFound from './views/PageNotFound.vue' 

Vue.use(Router)


let router = new Router({
    mode: 'history',
    base: process.env.BASE_URL, 
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/user',
            name: 'user',
            component: App,
            meta: { 
                requiresAuth: true  
            },
            children: [
                {
                    path: '',
                    component: Check
                },
                { 
                    path: 'profile',
                    component: ChangePassword 
                },
                {
                    path: 'archive',
                    component: Archive 
                },
                {
                    path: 'check', 
                    component: Check
                } 
            ]
        },
        {
            path: '/manager', 
            name: 'manager',
            component: App,
            meta: { 
                requiresAuth: true,
                status: 'manager' 
            },
            children: [
                {
                    path: '',
                    component: Profile
                },
                {
                    path: 'profile',
                    component: Profile 
                },
                {
                    path: 'archive',
                    component: Archive
                },
                {
                    path: 'company',
                    component: Company
                },
                {
                    path: 'check-admin',
                    component: CheckAdmin  
                },
                {
                    path: 'check', 
                    component: Check
                },
                {
                    path: 'change-password',
                    component: ChangePassword
                }
            ]
        },
        {
            path: '*',
            name: 'PageNotFound',
            component: PageNotFound 
        },
    ]
})


router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) { 
        if (VueCookies.get('login') == null) {
            next({
                path: '/',
                params: { nextUrl: to.fullPath }
            }) 
        } else {
            let status = VueCookies.get('status')
            if (to.matched.some(record => record.meta.status)) {
                if (status === 'manager') {
                    next()
                }
                else {
                    next({ name: 'user' }) 
                }
            } else {
                next()
            }
        }
    } else {
        next() 
    }
})


const ifAuthenticated = (to, from, next) => {
    let login = VueCookies.get('login')
    console.log(login)
    if (login) {
        next()
        return
    }
    next('/')
} 

export default router
