<template lang="pug"> 
    .profile-item 
        .profile-item__section  
            .sm-personal-info
                .sm-personal-info__avatar 
                    .avatar.avatar_sz_58  
                        img(:src="profileData.photo").avatar__photo 
                .sm-personal-info__content  
                    .sm-personal-info__subtitle ФИО 
                    .sm-personal-info__title.sm-personal-info__title_fz_16 {{profileData.name}}
        .profile-item__section 
            .profile-item__grid
                .profile-item__grid-item 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Серия и номер паспорта
                            .profile-info-box__value {{profileData.passport}}
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Процентная ставка
                            .profile-info-box__value {{profileData.interestRate}} %
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Статус скоринга 
                            .profile-info-box__value(v-if="profileData.scoringStatus === true") Кредит выдан
                            .profile-info-box__value(v-if="profileData.scoringStatus === false") Кредит не выдан
                .profile-item__grid-item 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title ID заемщика
                            .profile-info-box__value {{profileData.idBorrower}} 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Срок кредита
                            .profile-info-box__value {{profileData.сreditЕerm}} 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Статус кредита
                            .profile-info-box__value(v-if="profileData.сreditStatus === true") Закрыт без просрочки
                            .profile-info-box__value(v-if="profileData.сreditStatus === false") Закрыт c просрочкой
                            
                            //- .profile-info-box__select 
                            //-     span.st-input.st-input_h_32.st-input_ff_bold {{profileData.сreditStatus}}
                            //-     //- <v-select class='v-select_h_32' v-model="сreditStatus" :options="[,'bar']"></v-select>  

                .profile-item__grid-item 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title ID кредита
                            .profile-info-box__value {{profileData.сreditId}} 
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Сумма кредита
                            .profile-info-box__value {{profileData.сreditSum}}  
                    .profile-item__line
                        .profile-info-box 
                            .profile-info-box__title Число дней просрочки
                            .profile-info-box__value {{profileData.overdue}} 


</template>



 <script>
    import vSelect from 'vue-select'


    export default { 
        name: "ProfileItem",
        props: ['profileData'], 
        components: { 
            'v-select': vSelect  
        }
    }

</script> 
 
 

 





